import React from 'react';
import EachSkill from './EachSkill';

const ViewSkills = ({skills, hideSkill}) => {
    const showSkills = skills.length ? (
        skills.map((skill, idx) => {
            return (
                <div key={skill.id} className="skills-container justify align no-background no-border">
                    <h1 className="header black-text small-text bold white-box cursor" onClick={() => {hideSkill(idx)}}>{skill.title}</h1>
                    <EachSkill skill={skill.all} show={skill.show}/>
                </div>
            );
        })
    ) : (
        <p>Loading skills ...</p>
    );
    return (
        <div className="skills-container auto-width box-shadow margin-top-2">
            <h1 className="header white-text medium-text berlin">SKILLS<span className="orange-text">.</span></h1>
            {showSkills}
        </div>
    );
}

export default ViewSkills